'use client';

import { useState, useEffect } from 'react';
import { getMessagesForRecipient, type Message } from '@/lib/data';
import { notFound } from 'next/navigation';
import { MessageCard } from '@/components/MessageCard';
import { Header } from '@/components/Header';
import { Button } from '@/components/ui/button';
import Link from 'next/link';
import { ChevronLeft } from 'lucide-react';
import { formatDistanceToNow } from 'date-fns';
import { Skeleton } from '@/components/ui/skeleton';

type PageProps = {
  params: {
    name: string;
  };
};

export default function BottlePage({ params }: PageProps) {
  const [messages, setMessages] = useState<Message[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const recipientName = decodeURIComponent(params.name);

  useEffect(() => {
    async function fetchMessages() {
      setIsLoading(true);
      try {
        const fetchedMessages = await getMessagesForRecipient(recipientName);
        setMessages(fetchedMessages);
        if (fetchedMessages.length === 0) {
          // If you want to show a not found page for recipients with no messages
          // notFound();
        }
      } catch (error) {
        console.error("Failed to fetch messages:", error);
      } finally {
        setIsLoading(false);
      }
    }
    fetchMessages();
  }, [recipientName]);

  if (isLoading) {
    return (
      <div className="flex min-h-dvh flex-col">
        <Header />
        <main className="flex-1">
          <div className="container mx-auto max-w-2xl px-4 py-8 md:py-16">
             <Skeleton className="mb-4 h-6 w-48" />
             <Skeleton className="mb-8 h-10 w-64" />
             <div className="space-y-4">
                <Skeleton className="h-32 w-full" />
                <Skeleton className="h-32 w-full" />
                <Skeleton className="h-32 w-full" />
             </div>
          </div>
        </main>
      </div>
    )
  }

  return (
    <div className="flex min-h-dvh flex-col">
      <Header />
      <main className="flex-1">
        <div className="container mx-auto max-w-2xl px-4 py-8 md:py-16">
          <div className="mb-4">
            <Button
              asChild
              variant="link"
              className="pl-0 text-muted-foreground"
            >
              <Link href="/browse">
                <ChevronLeft className="mr-1 h-4 w-4" />
                Back to all bottles
              </Link>
            </Button>
          </div>
          <h1 className="truncate font-headline text-3xl font-bold tracking-tighter sm:text-4xl">
            Bottle for <span className="capitalize">{recipientName}</span>
          </h1>

          <div className="mt-8 space-y-4">
            {messages.map((message, index) => (
              <Link href={`/message/${message.id}`} key={message.id}>
                <MessageCard
                  content={message.content}
                  timestamp={
                    message.timestamp
                      ? formatDistanceToNow(new Date(message.timestamp), {
                          addSuffix: true,
                        })
                      : 'Just now'
                  }
                  style={{ animationDelay: `${index * 150}ms` }}
                  className="animate-in fade-in-0 slide-in-from-bottom-5 duration-500 fill-mode-both"
                />
              </Link>
            ))}
             {messages.length === 0 && !isLoading && (
              <p className="text-center text-muted-foreground">No messages in this bottle yet.</p>
            )}
          </div>
        </div>
      </main>
    </div>
  );
}
